import * as s from '../styles/PaymentInfoStyle';

function calculateValue(state: number): number {
  switch (state) {
    case 0:
      return 0;
    case 1:
      return 5000;
    case 2:
      return 3000;
    default:
      return 0; 
  }
}

export const PaymentInfo = ({ data }: any) => {
  return (
    <s.PaymentInfoContainer>
      <s.PaymentInfoTotalPrice>
        <s.PaymentInfoPriceTitle>  </s.PaymentInfoPriceTitle>
        <s.PaymentInfoPrice>{(data.salePrice * data.quantity).toLocaleString()} </s.PaymentInfoPrice>
      </s.PaymentInfoTotalPrice>
      <s.PaymentInfoDeliveryPrice>
        <s.PaymentInfoPriceTitle></s.PaymentInfoPriceTitle>
        <s.PaymentInfoPrice>{(calculateValue(data.delivery)).toLocaleString()} </s.PaymentInfoPrice>
      </s.PaymentInfoDeliveryPrice>
      <s.PaymentInfoTotalPriceBox>
        <s.TotalPriceTitle>  </s.TotalPriceTitle>
        <s.TotalPrice>{(data.salePrice * data.quantity+calculateValue(data.delivery)).toLocaleString()} </s.TotalPrice>
      </s.PaymentInfoTotalPriceBox>
    </s.PaymentInfoContainer>
  );
};

export default PaymentInfo;
